</template>

<script>
export default {
  data() {
    return {
      roadmap: [
        {
          title: "launch",
          text: "Membership Pass launch and marketing",
          imgUrl: require("../assets/roadmap/Pass.png"),
        },
        {
          title: "Podcast",
          text: "Podcast officially launching",
          imgUrl: require("../assets/roadmap/Podcast.png"),
        },
        {
          title: "WhitePaper",
          text: "Publish Whitepaper and tokenomics",
          imgUrl: require("../assets/roadmap/whitepaper.png"),
        },
        {
          title: "Airdrop",
          text: "Genesis XIE DAO NFT and token drop",
          imgUrl: require("../assets/roadmap/airdrop.png"),
        },
        {
