          text: "Charity  and Southeast Asia Subdao",
          imgUrl: require("../assets/roadmap/charity.png"),
        },
      ],
    };
  },
  mounted() {
    this.gsap
      .timeline({
        scrollTrigger: {
          trigger: ".roadmap-text",
          start: "center bottom",
          end: "bottom top",
          scrub: true,
        },
      })
      .to(".roadmap-text", {
        backgroundPosition: "0% 100%",
      });

