      .timeline({
        scrollTrigger: {
          trigger: ".rm-container",
          start: "top center",
        },
      })
      .from([".rm-container"], {
        // width: 1000,
        autoAlpha: 0,
        duration: 0.5,
      })
      .from([".rm-box-pl"], {
        autoAlpha: 0,
        stagger: 0.3,
      });
